.App {
  text-align: center;
  background-color: black;
  /* height: 100%; */
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}

/* This gets Google to fall into place */
.social {
	font-size: 1px;
}

/* Set an optional width for your button wrappers */
.social span {
	display: inline-block;
	width: 110px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: rgb(2, 2, 2);
  height: 150px;
  padding: 20px;
  color: rgb(166, 133, 197);
}

.App-intro {
  font-size: large;
}

.App-universe{
  height: 100%;
  padding: 0 0 0 0;
  margin: 0;
  margin-left: 2%;
  margin-right: 2%;  
  height: 90vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc(var(--vh, 1vh) * 90);
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.App-description{
  text-align: justify;  
}

.App-description a{
  color: gray;
}

.App-description p{
  color: rgb(93, 93, 93);
}

.App-description-container{
  margin: 30px 60px 30px 60px;  
}

@media screen and (min-width: 767px) {
  .App-description-container div{
    padding: 20px;
  }
}

.App-description-container.Responsive-wrapper{
  margin: 0;  
}

.App-author{
  text-align: right;
}

@media screen and (min-width: 767px) {
  .App-BarChart div{
      margin-top: 20px;
  }
}

@media screen and (max-width: 767px) {
  .App-BarChart div{
      margin-top: 5px;
  }
}